import { Button, Input } from "@mui/material";
import { useContext } from "react";
import { GlobalState } from "../../Context/Context";

import StatusSelect from "./StatusSelect";
import TypeOfPet from "./TypeOfPet";

export default function AdvanceSearch({
  setPetHeight,
  setPetName,
  setPetWeight,
  setStatus,
  adoptionStatus,
  setType,
  type,
  getTypePet,
  petName,
  petHeight,
  petWeight,
  setupdateStatusApi,
}) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
      }}
    >
      <div style={{ paddingBottom: 10, alignSelf: "center" }}>
        <div
          style={{
            padding: 5,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Input
            value={petName}
            style={{ padding: 10 }}
            placeholder="Name"
            onChange={(e) => setPetName(e.target.value)}
          ></Input>
          <Input
            value={petHeight}
            onChange={(e) => setPetHeight(e.target.value)}
            style={{ padding: 10 }}
            placeholder="Height"
          ></Input>
          <Input
            value={petWeight}
            onChange={(e) => setPetWeight(e.target.value)}
            style={{ padding: 10 }}
            placeholder="Weight"
          ></Input>
        </div>
      </div>
      <div style={{ padiding: 5 }}>
        <TypeOfPet type={type} setType={setType} />
      </div>
      <div style={{ padiding: 5 }}>
        <StatusSelect adoptionStatus={adoptionStatus} setStatus={setStatus} />
        <Button
          onClick={() => {
            setupdateStatusApi(false);
            getTypePet();
          }}
          style={{ color: "#8F3013" }}
        >
          Submit
        </Button>
      </div>
    </div>
  );
}
