import { Paper } from "@mui/material";
import React, { useContext, useState, useEffect } from "react";
import PetList from "../../components/PetList";
import AdvanceSearch from "../../components/search/AdvSearch";
import BasicSearch from "../../components/search/BasicSearch";
import Toggler from "../../components/search/Toggler";
import { GlobalState } from "../../Context/Context";

import { getPets } from "../../lib/lib";

function SearchPage() {
  const [advSearch, setAdvanceSearch] = useState(false);
  const [data, setData] = useState([]);
  const [type, setType] = useState("");
  const [petName, setPetName] = useState("");
  const [petHeight, setPetHeight] = useState("");
  const [petWeight, setPetWeight] = useState("");
  const [adoptionStatus, setStatus] = useState("");
  const {
    setLoading,
    change,
    setChange,
    loading,
    setupdateStatusApi,
    updateStatusApi,
  } = useContext(GlobalState);
  const [firstCall, setFirstCall] = useState(true);
  const [pageNumber, setPageNumber] = useState(0);

  // first forget password link
  // form to enter email
  // then next form to enter otp and new password

  const getRequest = async () => {
    setFirstCall(false);
    let params;
    //this conidition will only run if its not first call and not updateStatusAPi
    // let page;
    setLoading(true);
    if (advSearch) {
      params = {
        petName,
        petHeight,
        petWeight,
        adoptionStatus,
        type,
      };
    } else {
      params = {
        type,
      };
    }

    try {
      let response = await getPets(params, pageNumber + 1);
      if (!updateStatusApi) {
        setData((prev) => [...prev, ...response.pet]);
      } else {
        setData(response.pet);
      }
    } catch (err) {
      console.log(err);
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    if (!firstCall) {
      getRequest();
    }
  }, [change]);
  return (
    <div style={{ padding: 30 }}>
      <Paper
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          marginTop: 10,
          padding: 30,
          overflow: "hidden",
          borderRadius: 10,
          width: 350,
          height: 400,
          margin: "auto",
          alignSelf: "center",
          opacity: ".8",
        }}
        elevation={3}
      >
        <Toggler advSearch={advSearch} setAdvanceSearch={setAdvanceSearch} />
        {advSearch ? (
          <AdvanceSearch
            petName={petName}
            petHeight={petHeight}
            petWeight={petWeight}
            setPetHeight={setPetHeight}
            setPetName={setPetName}
            setPetWeight={setPetWeight}
            setStatus={setStatus}
            setType={setType}
            type={type}
            setupdateStatusApi={setupdateStatusApi}
            adoptionStatus={adoptionStatus}
            getTypePet={getRequest}
          />
        ) : (
          <BasicSearch
            getTypePet={getRequest}
            setupdateStatusApi={setupdateStatusApi}
            type={type}
            setType={setType}
          />
        )}
      </Paper>
      {!data ? null : <PetList data={data} setPageNumber={setPageNumber} />}
    </div>
  );
}

export default SearchPage;
