import React, { useContext } from "react";
import { GlobalState } from "../Context/Context";
import AppCard from "./Card";
function PetList({ data, setPageNumber }) {
  const { setChange, change, setLoading, loading } = useContext(GlobalState);
  // todo front end pagination
  //todo
  const scrollHandler = (e) => {
    const bottom =
      e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;

    if (bottom) {
      if (!loading) {
        setPageNumber((prev) => prev + 1);
        setChange(!change);
      }
    }
  };
  return (
    <div
      onScroll={scrollHandler}
      style={{
        display: "flex",
        // flexDirection: "column",
        flexWrap: "wrap",
        justifyContent: "center",
        alignItems: "center",
        overflowY: "scroll",
        maxHeight: "600px",
      }}
    >
      {data.map((pet, index) => (
        <AppCard
          key={index}
          name={pet.petName}
          imageUrl={pet.picture}
          bio={pet.bio}
          id={pet._id}
          breed={pet.breed}
          adoptionStatus={pet.adoptionStatus}
        />
      ))}
    </div>
  );
}

export default PetList;
